.charBox {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-weight: 500;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
}

@media only screen and (max-width: 768px) {
    .charBox {
        font-size: 40px;
        line-height: 60px;
        height: 60px;
        width: 60px;
    }
}

@media only screen and (min-width: 768px) {
    .charBox {
        font-size: 50px;
        line-height: 80px;
        height: 80px;
        width: 80px;
    }
}
